.litomobile_container {
  position: fixed;
  width: 0;
  background: gray;
  overflow: auto;
  z-index: 1001;
  max-height: 100%; }

.litomobile_menu {
  position: absolute;
  padding: 0;
  margin: 0; }
  .litomobile_menu li {
    display: block;
    padding: 15px 15px 15px 42px; }
    .litomobile_menu li.submenu_opened {
      background: rgba(0, 0, 0, 0.5); }
    .litomobile_menu li ul {
      height: 0;
      display: block;
      overflow-y: hidden;
      transition: 1s;
      padding-left: 0; }
      .litomobile_menu li ul li {
        padding-left: 0; }

.litomobile_right {
  left: auto;
  right: 0;
  height: 100%;
  top: 0; }
  .litomobile_right .litomobile_menu {
    left: 0; }

.litomobile_left {
  left: 0;
  right: auto;
  height: 100%;
  top: 0; }
  .litomobile_left .litomobile_menu {
    right: 0; }

.litomobile_top {
  top: 0;
  width: 100%;
  height: 0;
  bottom: auto; }
  .litomobile_top .litomobile_menu {
    width: 100%;
    text-align: center; }
    .litomobile_top .litomobile_menu li {
      padding: 15px; }

.litomobile_bottom {
  bottom: 0;
  width: 100%;
  top: auto;
  height: 0; }
  .litomobile_bottom .litomobile_menu {
    top: 0;
    width: 100%;
    text-align: center; }
    .litomobile_bottom .litomobile_menu li {
      padding: 15px; }

.litomobile_icon {
  width: 30px;
  height: 20px;
  position: relative;
  cursor: pointer;
  transition: 1s; }
  .litomobile_icon::after, .litomobile_icon::before,
  .litomobile_icon > div {
    content: '';
    display: block;
    width: 100%;
    height: 20%;
    border-radius: 30px;
    position: absolute;
    background: black;
    top: 0;
    transition: 0.3s; }
  .litomobile_icon::before {
    top: auto;
    bottom: 0; }
  .litomobile_icon > div {
    top: 50%; }
  .litomobile_icon_open {
    transform: rotate(45deg); }
    .litomobile_icon_open > div:first-of-type {
      transform: rotate(-90deg); }
    .litomobile_icon_open::after, .litomobile_icon_open::before {
      opacity: 0; }
    .litomobile_icon_open_om::before, .litomobile_icon_open_om::after {
      display: none; }

.litomobile_light.litomobile_container {
  background: #c2c2c2; }
  .litomobile_light.litomobile_container .litomobile_menu {
    padding: 0; }
    .litomobile_light.litomobile_container .litomobile_menu li:hover,
    .litomobile_light.litomobile_container .litomobile_menu li > *:hover {
      background: #646464;
      color: whitesmoke; }
    .litomobile_light.litomobile_container .litomobile_menu li {
      color: #6e6e6e; }
      .litomobile_light.litomobile_container .litomobile_menu li a {
        color: inherit;
        text-decoration: none; }

